import React, { Component } from 'react';
import styles from './style.module.css';

class SocialCard extends Component {
  render() {
    return (
        <div className={styles["socialCard"]}>
            <div className={styles["img"]} style={{backgroundImage: "url('"+this.props.img+"')"}}/>
            <div className={styles["desc"]}>
                <span className={styles["title"]}>{this.props.title}</span><br/>
                <span className={styles["subtitle"]}>{this.props.subtitle}</span><br/><br/>
                <span className={styles["text"]}>{this.props.text}</span>
            </div>
        </div>
    );
  }
}

export default SocialCard;
